.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff7ed;
  padding: 0px 20px 0px 20px;
  height: 75px;
  z-index: 99;
}

.header__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

header a {
  text-decoration: none;
  color: black;
  font-weight: 600;
  margin-left: 20px;
}

.header__nav-item--active {
  text-decoration: underline;
  text-decoration-thickness: 2px;
  text-underline-offset: 2px;
}
